*{
  margin: 0px;
  padding: 0px;
}
header{ 
  padding: 20px;
}
#company-logo{
  font-size: 2rem;
  color:#7f00cf;
  font-weight: bold;
}
.bg-body-tertiary {
  --bs-bg-opacity: 1;
  background-color: rgb(255, 255, 255) !important;
}
.btn-contact {
  background-color: #000000;
  color: white;
  font-size: 1.4rem;
  font-weight: bold;
  border-radius: 5px;
  padding: 5px;
  width: 200px;
}

#span {
  color:#7f00cf;
}


.banner-container {
  text-align: center;
  margin-top: 140px;
}

.banner-image-container {
  width: 90%;
  display: block;
  margin: auto;

}

.header-banner{
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight:bolder;
  font-size: 4.5rem;
  color: black;
}

.heroSecond-container{
  background-color: #7f00cf;
  height: 300px;
  margin-top: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}


.textHeader {
  text-align: center;
  color: white;
  font-size: 2.3rem;
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;

}

.container-of-aboutUs {
  margin: 100px;
}

.left-aboutus-container{
  background-color: black;
  padding: 40px;
  display: flex; /* Add flex display */
    align-items: center;
}
.left-aboutus-container p {
  color: white;
  font-size: 1.2rem;
  
}

.container-inner-aboutus {
  display: flex;
}

.aboutus-img{
  width: 100%;
  
}


#technology-header{
  text-align: center;
  color: white;
  
}

.technology-container{
  background-color: #7f00cf;
  padding: 50px;
  
}

.card-container{
  display: flex;
  flex-direction: row;
  gap: 50px;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

.card{
  background-color: rgb(16 16 16);
  color: white;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.card:hover{
     background-color: rgb(3, 3, 3);
     transform: scale(1.05); /* Add scale transform for the popup effect */
}


.img-specalized {
  height: 200px;
  width: 250px;
  display: block;
  margin:auto;
}

.free-banner-container {
  background-color: #7f00cf;
  padding: 10px;
  margin-top: 40px;
  margin-bottom: 40px;
  color: white;
  
}

.free-banner-container:hover {
  background-color: #4e007f;
  
}
.free-banner-container h1{
  text-align: center;
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight:bolder;

}

#contact-text{
  text-align: center;
  
}
.package-offer{
  color: white;
}

hr{
  border: 4px solid rgb(0, 0, 0);
}

/* Create two equal columns that floats next to each other */
.column {
  float: left;
  width: 50%;
  padding: 10px;
  height: 300px; /* Should be removed. Only for demonstration */
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.contact-header {
  color: white;
}


.form-container{
  background-color: white;
  padding: 30px;
}

.container-contact{
  display: flex;
  justify-content: center;
  align-items: center;
}

.row {
  justify-content: center;
  align-items: center;
}

.about-content {
  background: white;
  padding: 50px;
}

.left-contact{
  background-color: white;
  padding: 40px;
}

.card {
  height: auto;
}

form {
  background: black;
  padding: 50px;
  color: white;
  border-radius: 10px;
  font-family: 'Inter';
}

span.navbar-toggler-icon {
  background:#ffffff;
}
a.nav-link {
  display: block;
  margin: auto;
}
.navbar-toggler:not(:disabled):not(.disabled) {
  cursor: pointer;
  background-color: #000000;
}

/* BannerHero.css */

/* Style the video container */
.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

/* Style the content overlay */
.content-overlay {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  /* Add other styling for your content (font, spacing, etc.) */
}

.faq-container{
  padding: 90px;
  background: #acacff;
  color: white;
}
.faq-container h1{
  margin-bottom: 20px;
}


/**Max width media queries **/

@media screen and (max-width: 1090px) {
  .card-container {
    display: grid;
  }
  .card{
    width: 25rem;
  }
  .container-inner-aboutus{
    flex-direction: column;
  }
}

@media screen and (max-width: 1188px){
  .header-banner{
    font-size: 3.3rem;
  }
  .textHeader{
    font-size: 1.7rem;
  }
}

@media screen and (max-width: 874px){
  .header-banner{
    font-size: 2.5rem;
  }
  .banner-container{
    margin-top: 50px;
  }
  .heroSecond-container{
    margin-top: 100px;
    height: 200px;
  }
  .container-of-aboutUs{
    margin:0px;
  }
  .container-of-aboutUs h1 { 
    margin-left: 50px;
    margin-top: 20px;
  }
}

@media screen and (max-width: 671px){
  .header-banner{
    font-size: 2rem;
  }

  .textHeader{
    font-size: 1.5rem;
  }

}
@media screen and (max-width: 575px){
  header {
    padding: 30px;
}
.faq-container{
  padding: 20px;
}

.card{
  width: 19rem;
}
}